* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

.ReactModal__Overlay {
  background: none !important;
  position: static !important; }

.ReactModal__Content {
  border: none !important;
  background: none !important;
  position: static !important; }

.main {
  display: flex; }

.logo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: #0000001A 0% 0% no-repeat padding-box;
  line-height: 21px;
  letter-spacing: 3px;
  padding-left: 40px;
  height: 70px;
  font-weight: bold;
  font-size: 15px;
  font-family: Source Sans Pro,Bold; }

.sidebar {
  width: 20%;
  background-color: #43425c;
  color: white;
  position: fixed;
  height: 100vh; }

.content {
  margin-left: 20%;
  width: 80%; }

.calendar {
  margin-top: 50px; }

.list {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%; }

.list button {
  display: flex;
  background: none;
  border: none;
  outline: none;
  color: white;
  margin: 7px 5px;
  font-size: 16px;
  text-align: center;
  padding-top: 5px;
  height: 40px;
  font-weight: 300; }
  .list button:hover {
    background: #0000001A 0% 0% no-repeat padding-box;
    transition: .3s; }

.list i {
  margin-top: -3px;
  margin-right: 10px;
  margin-left: 50px;
  color: #A5A4BF; }

.current-window {
  font-size: 28px;
  margin-top: 100px;
  margin-left: 5%;
  color: #43425D; }

.top-bar {
  height: 70px;
  width: 100%;
  background: white;
  position: fixed;
  display: flex; }
  .top-bar input {
    width: 100%;
    height: 100%;
    border: none;
    outline: none;
    font: Regular Source Sans Pro;
    font-size: 20px;
    color: grey;
    padding-left: 40px; }
  .top-bar .search {
    position: absolute;
    left: 0;
    top: 35%;
    color: grey;
    font-size: 30px; }

.search-pool {
  width: 60%; }

.actions-pool {
  display: flex;
  width: 40%; }
  .actions-pool img {
    margin-top: 5px;
    height: 60px; }
  .actions-pool button {
    border: none;
    width: 10%;
    background: none;
    outline: none; }
    .actions-pool button:hover {
      background: grey;
      transition: 0.3s; }
  .actions-pool i {
    font-size: 30px;
    color: #43425D; }
  .actions-pool select {
    border: none;
    outline: none;
    background: none;
    color: #43425D; }
